import React from 'react'
import { useHistory } from 'react-router-dom'
import page_01 from '../images/pages-demo/01.png'
import page_02 from '../images/pages-demo/02.png'
import page_03 from '../images/pages-demo/03.png'
import page_04 from '../images/pages-demo/04.png'
import page_05 from '../images/pages-demo/05.png'
import page_06 from '../images/pages-demo/06.png'
import page_07 from '../images/pages-demo/07.png'
import page_08 from '../images/pages-demo/08.png'
import page_09 from '../images/pages-demo/09.png'
import page_10 from '../images/pages-demo/10.png'
import page_11 from '../images/pages-demo/11.png'
import page_12 from '../images/pages-demo/12.png'

const images = [
  { src: page_01, url: 'page_01', alt: 'page-demo-page_01' },
  { src: page_02, url: 'page_02', alt: 'page-demo-page_02' },
  { src: page_03, url: 'page_03', alt: 'page-demo-page_03' },
  { src: page_04, url: 'page_04', alt: 'page-demo-page_04' },
  { src: page_05, url: 'page_05', alt: 'page-demo-page_05' },
  { src: page_06, url: 'page_06', alt: 'page-demo-page_06' },
  { src: page_07, url: 'page_07', alt: 'page-demo-page_07' },
  { src: page_08, url: 'page_08', alt: 'page-demo-page_08' },
  { src: page_09, url: 'page_09', alt: 'page-demo-page_09' },
  { src: page_10, url: 'page_10', alt: 'page-demo-page_10' },
  { src: page_11, url: 'page_11', alt: 'page-demo-page_11' },
  { src: page_12, url: 'page_12', alt: 'page-demo-page_12' },
]

const HomeScreen = () => {
  const history = useHistory()

  const changeRouter = (router) => {
    history.push(router)
  }

  return (
    <>
      <section className="section">
        <article className="section__container">
          <span className="section__title section__title--bottom-line margin-bottom">單頁式網頁展示</span>
          <ul className="single-pages-demo">
            {images.map(({ src, alt, url }) => (
              <li key={alt} onClick={() => changeRouter(`/pagedemo/${url}`)}>
                <img src={src} alt={alt} />
              </li>
            ))}
          </ul>
        </article>
      </section>
    </>
  )
}

export default HomeScreen
